import { Selection, D3ZoomEvent } from "d3";
import { Chart } from "./chart.abstract";
import * as d3 from 'd3';
import { ChartTooltipComponent } from "../../chart-tooltip/chart-tooltip.component";
import { DateTime, Interval } from "luxon";

export class GroupedBarChart extends Chart {
    originalData: any;

    // subgroup: Array<any> = [];

    override drawChart(): void {
        // 전체 svg 영역
        // this.svg = (d3.select(this.elementRef).select('#chart') as any)
        // .append('svg')
        // .attr("width", this.width + this.margin.left + this.margin.right)
        // .attr("height", this.height + this.margin.top + this.margin.bottom)

        this.createSvg();

        // 줌 적용
        this.svg.call(this.zoom.bind(this));
        
        // 실제 데이터가 그려질 영역
        this.g = this.svg.append('g')
        .attr('transform', `translate(${this.margin.left}, ${this.margin.top})`);
        
        // 클립패스 선언
        this.g.append('defs').append('clipPath')
            .attr('id', 'clip')
            .append('rect')
            .attr('width', this.width)
            .attr('height', this.height)

        // const interval = this.getIntervalDays(DateTime.now(), DateTime.now()) as string[]
        const interval = this.interval;
        
        // d3.csv('https://raw.githubusercontent.com/holtzy/D3-graph-gallery/master/DATA/data_stacked.csv').then( (data) => {

        // y축 범위 계산
        const y = d3.scaleLinear()
            .domain([0, d3.max(this.originalData, d => Number((d as any).requests))!])
            .range([this.height, 0])
            .nice();

        // Y축 그리기
        this.g.append('g')
            .attr('class', 'y-axis')
            .call(d3.axisLeft(y))
            .style('font-family', 'Pretendard-Regular')

        // x축 범위 계산
        this.x = d3.scaleBand()
            // .domain(this.groups)
            .domain(interval)
            .range([0, this.width])
            .padding(0.2)

        this.xAxis = d3.axisBottom(this.x).tickSizeOuter(0);

        // x축 그리기
        this.g.append('g')
            .attr('class', 'x-axis')
            .attr('transform', `translate(0, ${this.height})`)
            .attr("clip-path", "url(#clip)")
            .call(this.xAxis)
            .selectAll('text')
            .attr('transform', 'translate(-10,0)rotate(-60)')
            .style('text-anchor', 'end')
            .style('font-family', 'Pretendard-Regular')

        // 하나의 그룹 내에 그려질 바의 위치 계산
        this.subX = d3.scaleBand()
            .domain(this.subgroups)
            .range([0, this.x.bandwidth()])
            .padding(0.05);

        // 각 바에 색 지정
        const color = d3.scaleOrdinal()
            .domain(this.subgroups)
            .range(d3.schemeCategory10)

        
        // console.log(this.data);
        
        // 그래프에 바 그리기
        this.g.append('g')
            .attr("clip-path", "url(#clip)")
            .selectAll('g')
            .data(this.data)
            .join('g')
            .attr('class', 'bar-group')
            .attr('transform', (d:any) => {
                // console.log(d);
                
                // 여기서 들어오는 data/d는 데이터 하나하나의 row가 들어옴
                // {date: '2024-05-01', login: 2, monitoring: 3, main: 0}
                return `translate(${this.x(d.date)}, 0)`
            }) // x축의 위치 조정
            .selectAll('rect')
            .data((d: any, i) => {
                // 각 객체마다 하나의 bar를 그려야 하므로 새로운 형태의 객체로 정제
                return this.subgroups.map(key => {
                    return { key: key, value: d[key]}
                })
                // return [{ key: d.url, value: d.requests, group: i }];
            })
            .join('rect')
            .attr('class', 'mybar')
            .attr('x', (d: any) => {
                return this.subX(d.key)!;
            })
            .attr('y', (d: any) => this.height)
            .attr('width', this.subX.bandwidth())
            .attr('height', (d: any) => 0)
            .attr('fill', (d: any) => color(d.key) as any)
            .on('mouseover', (d: any) => this.mouseOver(d))
            .on('mousemove', d => this.mouseMove(d))
            .on('mouseout', d => this.mouseOut(d));

        
        // 애니메이션 적용
        this.g.selectAll(".mybar")
            .transition()
            .duration(300)
            .attr("y", (d: any) => {
                // console.log('데이터', d, 'y는', y(d.value));
                return y(d.value);
            })
            .attr("height", (d: any) => this.height - y(d.value))
            .delay((d, i) =>  (i * 10))

        // 범례 계산
        // const legendColor = d3.scaleOrdinal()
        //     .domain(this.subgroups)
        //     .range(d3.schemeSet1);

        // 범례 위치
        const legendPosition = {
            x: this.width - (this.margin.right * 1.5),
            y: this.margin.top
        }

        // 범례 그리기
        const size = 20
        const legendGroup = this.svg
            .append('g')
            .attr('opacity', 0.5);
        legendGroup
            .selectAll("mydots")
            .data(this.subgroups)
            .enter()
            .append("rect")
            .attr("x", legendPosition.x)
            .attr("y", function (d, i) { return legendPosition.y + i * (size + 5) }) // 100 is where the first dot appears. 25 is the distance between dots
            .attr("width", size)
            .attr("height", size)
            .style("fill", function (d: any) { return color(d) } as any)

        // 각 범례 라벨
        legendGroup.selectAll("mylabels")
            .data(this.subgroups)
            .join("text")
            .attr("x", legendPosition.x + size * 1.2)
            .attr("y", function (d, i) { return legendPosition.y + i * (size + 5) + (size / 2) }) // 100 is where the first dot appears. 25 is the distance between dots
            .style("fill", function (d: any) { return color(d) } as any)
            .text(function (d) { return d})
            .attr("text-anchor", "left")
            .style("alignment-baseline", "middle")
        // })

        this.drawGrid();
    }

    override zoom(svg: Selection<SVGSVGElement, unknown, HTMLElement, any>): void {
        const extent: [[number, number], [number, number]] = [[0, this.margin.top], [this.width, this.height - this.margin.top]];

        svg.call(
        d3.zoom<SVGSVGElement, unknown>()
            .scaleExtent([1, 15])
            .translateExtent(extent)
            .extent(extent)
            .on('zoom', this.zoomed.bind(this))
        )
    }

    override zoomed(event: D3ZoomEvent<HTMLElement, any>): void {
        this.x.range([0, this.width].map(d => event.transform.applyX(d)));
        this.subX.rangeRound([0, this.x.bandwidth()]);
        
        // console.log(this.svg.node()?.parentNode);
        
        this.svg.selectAll('.bar-group').attr('transform', (d: any) => {
        // console.log('transform 내부 ', d);
        return `translate(${this.x(d.date)},0)`;
        });
        this.svg.selectAll('.mybar').attr('x', (d: any) => {return this.subX(d.key)!}).attr('width', this.subX.bandwidth());

        // x축 업데이트
        this.svg.selectAll('.x-axis').call(this.xAxis as any);
    }

    override mouseOver(event: any): void {
        d3.select(event.target).style('opacity', 0.5);
        this.tooltip = this.container.createComponent(ChartTooltipComponent).instance;
        this.tooltip.x = event.clientX;

        this.tooltip.y = event.clientY;

        this.tooltip.data = event.target.__data__;
    }

    override mouseMove(event: any): void {
        this.tooltip.onMouseMove(event);
    }

    override mouseOut(event: any): void {
        d3.select(event.target).style('opacity', 1);
        this.container.clear();
    }

    override refineData(data: any, url?: any) {
        // subgroup
        // Nitrogen,normal,stress 이런 데이터를
        // login, monitoring 이것처럼 url별로
        // console.log('원래',data);

        this.originalData = data;

        const check = new Set();
        this.subgroups = data.filter((d: any) => {
            const cur = d.url;
            const exist = check.has(cur);
            check.add(cur);
            return !exist;
        }).map((d: any) => {
            return (d.url === undefined || d.url === '') ? 'main' : d.url
        });

        this.data = data.reduce((acc: any, cur: any) => {
            const existing = acc.find((d: any) => d.date === cur.date);

            const curUrl = cur.url === '' ? 'main' : cur.url;

            if(existing){
                // existing[curUrl] = (existing[curUrl] || 0) + parseInt(cur.requests, 10);
                existing[curUrl] = parseInt(cur.requests, 10);
            }else {
                const newItem = { date: cur.date, [curUrl]: parseInt(cur.requests, 10)};
                acc.push(newItem);
            }

            return acc;
        }, [])
        // console.log('정제1',this.data);

        this.data = this.data.map((item: any) => {
            this.subgroups.forEach(url => {
                if (!(item[url])) {
                    // console.log(this.subgroups);

                    item[url] = 0;
                }
            })
            return item;
        });
        // console.log('정제 데이터', this.data);
        

        this.sortDataByDate(this.data);
    }

    sortDataByDate(data: any){
        return data.sort((a: any, b: any) => {
            const dateA = DateTime.fromISO(a.date);
            const dateB = DateTime.fromISO(b.date);
            return dateA.toMillis() - dateB.toMillis();
        })
    }

    // 선택한 달의 날짜 배열 구하기
    getIntervalDays(start: DateTime, end: DateTime){
        return Interval.fromDateTimes(
            start.minus({ month: 1 }).startOf('month'),
            end.minus({ month: 1 }).endOf('month')
        ).splitBy({
            day: 1
        }).map((date: Interval) => date.start?.toISODate())
    }

}