import { Selection, D3ZoomEvent } from "d3";
import { Chart } from "./chart.abstract";
import * as d3 from 'd3';
import { ChartTooltipComponent } from "../../chart-tooltip/chart-tooltip.component";

export class HorizontalStackBar extends Chart {
    stackX: d3.ScaleLinear<number, number, never> = {} as d3.ScaleLinear<number, number, never>;
    stackY: d3.ScaleBand<string> = {} as d3.ScaleBand<string>;
    stackYAxis: d3.Axis<string> = {} as d3.Axis<string>;
    stackXAxis: d3.Axis<d3.NumberValue> = {} as d3.Axis<d3.NumberValue>;

    override drawChart(): void {
        this.margin.left = this.margin.left + 15;

        this.svg = (d3.select(this.elementRef).select('#chart') as any)
            .append('svg')
            .attr('width', this.width + this.margin.left + this.margin.right)
            .attr('height', this.height + this.margin.top + this.margin.bottom)
            .call(this.zoom.bind(this));

        this.g = this.svg.append('g')
            .attr('transform',
            `translate(${this.margin.left}, ${this.margin.top})`
            );

        // 클립패스 설정
        this.g.append("defs").append("clipPath")
            .attr("id", "stacked-bar-clip")
            .append("rect")
            .attr("width", this.width)
            .attr("height", this.height)


        d3.csv("https://raw.githubusercontent.com/holtzy/D3-graph-gallery/master/DATA/data_stacked.csv").then((data) => {
        const subgroups = data.columns.slice(1);

        this.groups = data.map((d:any) => {
            // console.log(d);
            return d.group;
        });

        this.stackY = d3.scaleBand()
            .domain(this.groups)
            .range([this.height, 0])
            .padding(0.2);
            
        this.stackYAxis = d3.axisLeft(this.stackY).tickSizeOuter(0);

        this.stackX = d3.scaleLinear()
            .domain([0,60])
            .range([0, this.width]);

        this.stackXAxis = d3.axisBottom(this.stackX);

        this.g.append('g')
            .attr('class', 'x-axis')
            .attr("clip-path", "url(#stacked-bar-clip)")
            .attr('transform', `translate(0, ${this.height})`)
            .call(this.stackXAxis);


        this.g.append('g')
            .call(this.stackYAxis)
            .selectAll('text')
            .attr('transform', `translate(-15,0)`)
            .style('text-anchor', 'middle');

        const color = d3.scaleOrdinal()
            .domain(subgroups)
            .range(['#e41a1c', '#377eb8', '#4daf4a']);

        const stackedData = d3.stack()
            .keys(subgroups)(data as any);

        console.log('stackedData', stackedData);
        
        /**
        group,Nitrogen,normal,stress
        banana,12,1,13
        poacee,6,6,33
        sorgho,11,28,12
        triticum,19,6,1
         */

        this.g.append('g')
            .attr('class', 'stacked-bar')
            .attr("clip-path", "url(#stacked-bar-clip)")
            // 현재 선택되어 있는 자식 요소중에서 .stacked-bar를 지닌 요소를 선택
            // 즉, 바로 앞전 코드에서 선택한 요소는 미포함됨
            .selectAll('.stacked-bar')
            .data(stackedData)
            .join('g')
                .attr('fill', (d: any) => color(d.key) as any)
                .selectAll('rect')
                // d[0]: 시작위치
                // d[1]: 끝위치
                // d[2]: 데이터
                .data(d => d)
                .join('rect')
                    .attr('class', 'mybar')
                    .attr('x', (d: any) => {
                        // console.log('x좌표', this.stackX(d[0]), '데이터', d.data);
                        return this.stackX(d[0])!
                    })
                    .attr('y', (d: any) => this.stackY(d.data.group)!)
                    .attr('height', this.stackY.bandwidth())
                    .attr('width', d => this.stackX(d[1]) - this.stackX(d[0]))
                    .on('mouseover', (d: any) => this.mouseOver(d))
                    .on('mousemove', (d: any) => this.mouseMove(d))
                    .on('mouseout', (d: any) => this.mouseOut(d))
            // .transition()
            //     .duration(800)
                // .attr('y', d => y(d[1]))
                // .attr('height', d => y(d[0]) - y(d[1]));
        });
    }

    override zoom(svg: Selection<SVGSVGElement, unknown, HTMLElement, any>): void {
        const extent: [[number, number], [number, number]] = [[0, this.margin.top], [this.width - this.margin.right, this.height - this.margin.top]];

        svg.call(
        d3.zoom<SVGSVGElement, unknown>()
            .scaleExtent([1, 8])
            .translateExtent(extent)
            .extent(extent)
            .on('zoom', this.zoomed.bind(this))
        )
    }

    override zoomed(event: D3ZoomEvent<HTMLElement, any>): void {
      this.stackX.range([0, this.width - this.margin.right].map(d => event.transform.applyX(d)));
      this.svg.selectAll('.mybar').attr('x', (d: any) => {
          return this.stackX(d[0])!
      }).attr('width', (d: any) => this.stackX(d[1]) - this.stackX(d[0]));
    this.svg.selectAll('.x-axis').call(this.stackXAxis as any);
    }

    override mouseOver(event: any): void {
        d3.select(event.target).style('opacity', 0.5);
        this.tooltip = this.container.createComponent(ChartTooltipComponent).instance;
        this.tooltip.x = event.clientX;
        this.tooltip.y = event.clientY;

        const parentNode = d3.select(event.target).node().parentNode;
        const data = d3.select(parentNode).datum();
        const subgroupName = (data as any).key;

        const subgroupValue = event.target.__data__.data[subgroupName];
        this.tooltip.data = { [subgroupName]: subgroupValue };
    }

    override mouseMove(event: any): void {
        this.tooltip.onMouseMove(event);
    }

    override mouseOut(event: any): void {
        d3.select(event.target).style('opacity', 1);
        this.container.clear();
    }

    override refineData(data: any, url?: any) {
        
    }

}