import { Component, ElementRef, OnInit, Renderer2, ViewChild, ViewContainerRef } from '@angular/core';
import * as d3 from 'd3';
import * as dummy from './dummy_data.json';
import { ChartTooltipComponent } from '../chart-tooltip/chart-tooltip.component';

@Component({
  selector: 'app-d3chart',
  templateUrl: './d3chart.component.html',
  styleUrl: './d3chart.component.scss'
})
export class D3chartComponent implements OnInit {

  @ViewChild('container', { read: ViewContainerRef }) container!: ViewContainerRef;
  @ViewChild('chart') chart!: ElementRef;

  constructor(private renderer: Renderer2){}

  ngOnInit() {
    this.dataArray = dummy.data_mini as Array<any>;
    this.getServerStatus();
  }

  getServerStatus() {
    this.visualizeData(dummy.data_mini as Array<any>);
    // console.log(dummy.data);
    
  }

  dataArray = new Array<any>();

  width = 1000;
  height = 500;
  marginTop = 70;
  marginRight = 60;
  marginLeft = 50;
  marginBottom = 80;

  svg: any;
  
  barGroup: any;

  xScale = d3.scaleBand();
  yScale = d3.scaleLinear();

  gx: any;
  gy: any;

  xAxis: any;
  yAxis: any;

  bar: any;

  line: any;
  path: any;
  dots: any;

  visualizeData(dataArray: Array<any>) {

    this.svg = d3.select('#chart').append('svg')
      .attr('width', this.width)
      .attr('height', this.height)
      .style('background', 'white')
      .attr('transform', `translate(${this.marginLeft})`)

    this.xScale = d3.scaleBand()
      .domain(dataArray.map((data,i) => i.toString()))
      .range([this.marginLeft, this.width - this.marginRight])

    this.yScale = d3.scaleLinear()
      .domain([0,d3.max(dataArray, d=> Math.max(d.cpuUsage, d.memoryUsage, d.diskUsage)) as number]).nice()
      .range([this.height - this.marginBottom, this.marginTop]);

    const leftLimit = this.svg.append("defs").append("clipPath")
      .attr("id", "clip")
      .append("rect")
      .attr("width", this.width - this.marginLeft - this.marginRight)
      .attr("height", this.height - this.marginTop - this.marginBottom)
      .attr("x", this.marginLeft)
      .attr("y", this.marginTop);


    this.barGroup = this.svg.append('g').attr('class', 'bar-group').attr("clip-path", "url(#clip)");

    ['cpuUsage', 'memoryUsage', 'diskUsage'].forEach((key,index) => {
      this.bar = this.barGroup.selectAll(`.bar-${key}`)
        .data(dataArray)
        .enter()
        .append('rect')
        .attr('class', `bar-${key}`)
        .style('stroke', 'black')
        .style('stroke-width', '0.5px')
        // x(i.toString())! : x축의 구간을 지정
        // (x.bandwidth()/3)*index : 지정된 구간에서 바를 그릴 위치
        // 예를 들어 10개의 구간이 있다면
        // 첫번째는, 10개의 구간 중 하나의 구간을 지정하는 역할
        // 두번째는, 그 하나의 구간을 다시 3개의 구간으로 나눠 바를 그릴 위치를 지정함
        .attr('x', (data: any,i: any) => this.xScale(i.toString())! + (this.xScale.bandwidth()/3)*index)
        .attr('y', (d:any) => {
          return this.yScale(0);
        })
        .attr('width', this.xScale.bandwidth()/3)
        .attr('height', 0)
        .attr('fill', 'white')
        .on('mouseover', (d: any) => this.barOver(d))
        .on('mouseout', (d: any) => this.barOut(d))
        .transition()
        .duration(500)
        .attr('fill', ['red', 'green', 'blue'][index])
        .attr('y', (d: any) => this.yScale(d[key]))
        .attr('height', (d: any) => (this.height - this.marginBottom) - this.yScale(d[key]))
    })

    this.xAxis = d3.axisBottom(this.xScale);
    this.yAxis = d3.axisLeft(this.yScale)

    this.gx = this.svg.append('g')
      .attr('class', 'x-axis')
      .attr('transform', `translate(0,${this.height - this.marginBottom})`)
      .call(this.xAxis)

    this.gy = this.svg.append('g')
      .attr('class', 'y-axis')
      .attr('yscale-margin', 'margin-left:20px')
      .attr('transform', `translate(${this.marginLeft},0)`)
      .call(this.yAxis)

    this.line = d3.line()
      .x((d, i) => this.xScale(i.toString()) as number + this.xScale.bandwidth() / 2)
      .y((d: any,index) => this.yScale(d.memoryUsage));

    this.path = this.svg.append('path')
      .datum(dataArray)
      .attr('fill', 'none')
      .attr('stroke', 'black')
      .attr('stroke-width', 2)
      .attr('d', this.line)
      .attr('class','line')
      .attr("clip-path", "url(#clip)");

    const totalLength = this.path.node()?.getTotalLength()

    console.log('길이 ', this.path.node());

    this.path
      // .attr('stroke-dasharray', 0)
      .attr('stroke-dashoffset', totalLength!)
      .transition()
      // .duration(500)
      // .ease(d3.easeLinear)
      // .attr('stroke-dasharray', totalLength)
      // .attr('stroke-dashoffset', totalLength);
    

    this.dots = this.svg.selectAll('.dot')
      .data(dataArray)
      .enter()
      .append('circle')
      .attr('class', 'dot')
      .attr('cx', (d: any, index: any) => this.xScale(index.toString())! + this.xScale.bandwidth() / 2)
      .attr('cy', (d:any) => this.yScale(d.memoryUsage))
      .attr('r', 5)
      .attr('fill', 'black')
      .attr("clip-path", "url(#clip)");

    const legendData = ['CPU Usage', 'Memory Usage', 'Disk Usage'];
    const colorScale = d3.scaleOrdinal<string,string>().domain(legendData).range(['red', 'green', 'blue']);

    // 범례 테두리 추가
    // 범례 항목의 크기와 간격을 고려하여 테두리의 위치와 크기 계산
    const legendPadding = 5; // 테두리와 범례 항목 사이의 패딩
    const legendWidth = 150; // 테두리의 너비, 필요에 따라 조정
    const legendHeight = legendData.length * 20 + legendPadding * 2; // 항목 수와 패딩을 기반으로 높이 계산

    // 전체 범례를 포함할 상위 g 요소의 위치를 설정
    const legendContainer = this.svg.append('g')
      .attr('class', 'legend-container')
      .attr('transform', `translate(${this.width - this.marginRight - legendWidth}, ${this.marginTop})`);

    // console.log('g : ',legendContainer);
    

    // 범례 데이터에 대한 g 요소 생성
    const legend = legendContainer.selectAll('.legend')
      .data(legendData)
      .enter()
      .append('g')
      .attr('class', 'legend')
      // 이제 각 범례 항목은 상위 g 요소(legendContainer) 내에서 상대적으로 위치합니다.
      .attr('transform', (d: any, i: any) => `translate(0, ${i * 20})`)

    // 각 범례 항목에 대한 rect 추가

    const rectangleWidth = 18;
    const rectangleHeight = 18;
    legend.append('rect')
      .attr('x', 0) // 상위 g 요소에 대해 상대적인 위치
      .attr('y', 0)
      .attr('width', rectangleWidth)
      .attr('height', rectangleHeight)
      .style('fill', (d: any) => colorScale(d))
      .style('stroke', 'black')
      .style('stroke-width', '0.5px');

    // 각 범례 항목에 대한 text 추가
    legend.append('text')
      .attr('x', rectangleWidth + legendPadding) // rect의 오른쪽에 텍스트를 위치시킵니다. 
      .attr('y', 9)
      .attr('dy', '.35em')
      .style('fill', 'black')
      .style('text-anchor', 'start') // 텍스트가 x 속성값의 오른쪽에서 시작하도록 설정
      .text((d: any) => d);

    // 범례 테두리 추가 (이전과 동일)
    this.svg.append("rect")
      .attr("class", "legend-border")
      .attr("x", this.width - this.marginRight - legendWidth - legendPadding)
      .attr("y", this.marginTop - legendPadding)
      .attr("width", legendWidth)
      .attr("height", legendHeight)
      .style("fill", "none")
      .style('stroke', 'black')
      .style('stroke-width', '0.5px')

      /** 브러시 추가 로직 */
    // this.brush = d3.brushX()
    //   .extent([[this.marginLeft, this.marginTop], [this.width - this.marginRight, this.height - this.marginBottom]])
    //   .on('end', (e) => this.brushedEnd(e));

    // this.brush = d3.brushX()
    //   .extent([[this.marginLeft, this.marginTop], [this.width - this.marginRight, this.height - this.marginBottom]])
    //   .on('end', (e) => this.brushedEnd(e));

    // this.brushGroup = this.svg.append('g')
    //   .attr('class', 'brush')
    //   .call(this.brush);

    /**드래그앤드롭 로직 */
    // this.svg.call(
    //   d3.drag()
    //     // .on('start', (d) => this.dragStart(d))
    //     // .on('drag', (d) => this.draged(d))
    //     .on('end', (d) => this.dragEnd(d))
    // )


    const zoom = d3.zoom()
      .scaleExtent([1,8])
      .translateExtent([
        [this.marginLeft, this.marginTop],
        [this.width - this.marginRight, this.height - this.marginTop]
      ])
      .on('zoom', (event) => this.zoomed(event))
      
      this.svg.call(zoom);
      
    }
    
  zoomed(event: d3.D3ZoomEvent<HTMLElement, any>){
    // ['cpuUsage', 'memoryUsage', 'diskUsage']
    console.log(event);
    this.xScale.range([this.marginLeft, this.width - this.marginRight].map(d => {
      console.log('dd',event.transform.applyX(d));
      
      return event.transform.applyX(d);
    }));
    
    ['cpuUsage', 'memoryUsage', 'diskUsage'].forEach((key, index) => {
      this.barGroup.selectAll(`.bar-${key}`)
      .attr('x', (d: any, i: any) => this.xScale(i.toString())! + (this.xScale.bandwidth() / 3) * index)
      .attr('width', this.xScale.bandwidth() / 3);
    })
    
    this.line = d3.line()
    .x((d, i) => this.xScale(i.toString()) as number + this.xScale.bandwidth() / 2)
    .y((d: any, index) => this.yScale(d.memoryUsage));
    
    this.path.attr('d', this.line);
    
    this.dots
    .attr('cx', (d:any, index: any) => this.xScale(index.toString())! + this.xScale.bandwidth() / 2)
    .attr('cy', (d:any) => this.yScale(d.memoryUsage))
    this.svg.selectAll(".x-axis").transition().duration(500).call(this.xAxis);
  }

  dragEnd(event: d3.D3DragEvent<HTMLElement, any, any>){
    console.log('end', event);
    // this.xScale.range([event.subject.x,event.x])
    const x0 = event.subject.x;
    const x1 = event.x;
    const newDomain = this.xScale.domain().filter((d: any, i: any) => {
      const xStartValue = this.xScale(i.toString());
      const xEndValue = xStartValue! + this.xScale.bandwidth();

      return x0 <= xEndValue && x1 >= xStartValue!;
    });

    const newArray = this.dataArray.slice(Number(newDomain[0]), Number(newDomain[-1]));

    this.xScale.domain(newDomain);

    /**
     * .domain([0,d3.max(dataArray, d=> Math.max(d.cpuUsage, d.memoryUsage, d.diskUsage)) as number]).nice()
      .range([this.height - this.marginBottom, this.marginTop]);
     */

    // this.yScale.domain([0, d3.max(dataArray, d => Math.max(d.cpuUsage, d.memoryUsage, d.diskUsage)) as number])

    ['cpuUsage', 'memoryUsage', 'diskUsage'].forEach((key, index) => {
      this.barGroup.selectAll(`.bar-${key}`)
        .attr('x', (d: any, i: any) => {
          console.log('dd/', this.xScale(i.toString())!);
          console.log('ss/', (this.xScale.bandwidth() / 3));
          
          return this.xScale(i.toString())! + (this.xScale.bandwidth() / 3) * index
        })
        .attr('width', this.xScale.bandwidth() / 3)
    })

    this.svg.selectAll(".x-axis").transition().duration(500).call(this.xAxis);
  }
    
  barOver(event: any){
    d3.select(event.target).style('opacity',0.5);
    // console.log('바', event.clientX);
    const instance = this.container.createComponent(ChartTooltipComponent).instance;
    instance.x = event.clientX;
    instance.y = event.clientY;
    instance.data = event.target.__data__;
  }
  
  barOut(event: any) {
    d3.select(event.target).style('opacity', 1);
    this.container.clear();
  }

  brushed(event: any){
    console.log('드래그시작',event);
    
  }

  brushedEnd(event: any){
    console.log('드래그 끝', event);
    
    const selection = event.selection;
    if(selection === null) return;
    
    const [x0, x1] = selection.map((d:any) => {
      // console.log(d);
      return d;
    });
    
    const selectedData = this.dataArray.filter((d:any, i:any) => {
      const xStartValue = this.xScale(i.toString());
      const xEndValue = xStartValue! + this.xScale.bandwidth();
      
      return x0 <= xEndValue && x1 >= xStartValue!;
    });
    
    console.log(selectedData);
    
    // this.brushGroup.call(this.brush.move, null);
    
    this.updateChart(selectedData);
  }

  updateChart(newArray: Array<any>){

    // console.log(this.chart.nativeElement);
    // console.log(newArray);
    
    
    // this.renderer.removeChild(this.chart.nativeElement, this.chart.nativeElement.firstElementChild);
  }

  // dragStart(event: any){
  //   console.log('start', event);
    
  // }

  // draged(event: any){
  //   // console.log('ongoing', event);
  // }

}
