import { AfterViewInit, Component, ElementRef, Input, OnInit, Renderer2, ViewChild } from '@angular/core';

@Component({
  selector: 'app-chart-tooltip',
  templateUrl: './chart-tooltip.component.html',
  styleUrl: './chart-tooltip.component.scss'
})
export class ChartTooltipComponent implements OnInit {
  @Input() data: any = '';
  @Input() x!: number;
  @Input() y!: number;
  dataArray: Array<{key: string, value: any}> = new Array();
  longText = `The Shiba Inu is the smallest of the six original and distinct spitz breeds of dog
  from Japan. A small, agile dog that copes very well with mountainous terrain, the Shiba Inu was
  originally bred for hunting.`;

  /**
   * 동적으로 컴포넌트를 생성할 때
   * { read: ElementRef }
   * read를 사용하여 읽어오고자 하는 데이터의 타입을 지정해줘야 함
   *  */ 
  @ViewChild('cardRef', { read: ElementRef }) cardRef!: ElementRef;

  constructor(private renderer: Renderer2){}

  ngOnInit(): void {
    this.setData();
  }

  setData(){
    Object.entries(this.data).forEach(([key, value]) => {
      this.dataArray.push({key, value});
    })
  }

  ngAfterViewInit(): void {
    
    const el = this.cardRef?.nativeElement;
    // console.log(this.cardRef);

    this.renderer.setStyle(el, 'x', this.x);
    this.renderer.setStyle(el, 'y', this.y);
    
    this.renderer.setStyle(el, 'left', `${this.x + 20}px`)
    this.renderer.setStyle(el, 'top', `${this.y + 50}px`);
  }

  onMouseMove(event: MouseEvent): void {
    this.x = event.clientX;
    this.y = event.clientY;

    const el = this.cardRef.nativeElement;
    this.renderer.setStyle(el, 'left', `${this.x + 20}px`)
    this.renderer.setStyle(el, 'top', `${this.y + 20}px`);
  }
}
