import { Component, ElementRef, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild, ViewContainerRef } from '@angular/core';
import { Chart } from './classes/chart.abstract';
import { Subscription, filter } from 'rxjs';
import { Store } from '@ngrx/store';
import { AppState } from '../../../../core/store/state.interface';
import { selectMonth, selectPageView } from '../../../../core/store/monitoring/monitoring.selector';
import { pvAction } from '../../../../core/store/monitoring/monitoring.action';
import { DateTime } from 'luxon';

@Component({
  selector: 'app-chart',
  templateUrl: './chart.component.html',
  styleUrl: './chart.component.scss'
})
export class ChartComponent implements OnInit, OnDestroy {

  constructor(
    public readonly elRef: ElementRef,
    private store: Store<AppState>
  ) {
  }

  isLoading: boolean = true;
  @Input('chart') chart!: Chart;
  @Input('path') path!: string;
  @Output() event: EventEmitter<void> = new EventEmitter();
  @ViewChild('chartContainer', {read: ViewContainerRef, static: true}) container!: ViewContainerRef; 
  pvDataSubscription!: Subscription;
  monthSubscription!: Subscription;
  month!: DateTime;

  ngOnInit(): void {
    this.initData();
  }

  initData() {
    // this.monitoringService.data$.pipe(
    //   filter(d => Object.keys(d).length > 0)
    // )
    // .subscribe({
    //   next: d => {
    //     this.isLoading = false;
    //     this.chart.refineData(d, this.path);
    //     this.chart.drawChart();
    //   }
    // })
    this.monthSubscription = this.store.select(selectMonth).subscribe({
      next: month => {
        this.isLoading = true;
        this.month = month;
        this.chart.removeChart();
        this.store.dispatch(pvAction());
      }
    })
    this.pvDataSubscription = this.store.select(selectPageView).pipe(
      filter(pvData => {
        return pvData.length > 0
      })
    ).subscribe({
      next: pvData => {
        // console.log('컴포넌트', this.path);
        this.isLoading = false;
        // const data = [...pvData];
        this.chart.setInterval(this.month);
        this.chart.refineData(pvData, this.path);
        this.chart.drawChart();
      }
    })
  }

  ngOnDestroy(): void {
    this.monthSubscription.unsubscribe();
    this.pvDataSubscription.unsubscribe();
  }
}
