import { Component, OnInit } from '@angular/core';
import { Store } from '@ngrx/store';
import { DateTime } from 'luxon';
import { AppState } from '../../../../core/store/state.interface';
import { monthUpdteAction } from '../../../../core/store/monitoring/monitoring.action';
import { selectMonth } from '../../../../core/store/monitoring/monitoring.selector';
import { map } from 'rxjs';

@Component({
  selector: 'app-month-picker',
  templateUrl: './month-picker.component.html',
  styleUrl: './month-picker.component.scss'
})
export class MonthPickerComponent implements OnInit {

  constructor(
    private store: Store<AppState>
  ){}

  month!: any;
  picker = DateTime.now();

  getLast(){
    this.store.dispatch(monthUpdteAction({month: this.picker.minus({month: 1})}));
  }

  getNext(){
    this.store.dispatch(monthUpdteAction({month: this.picker.plus({month: 1})}));
  }

  ngOnInit(): void {
    // this.store.dispatch(monthUpdteAction({month: DateTime.now()}));
    this.month = this.store.select(selectMonth).pipe(
      map(month => {
        this.picker = month;
        return month.toFormat('MMM');
      })
    )
  }
}
