import { Component, OnInit, ViewChild, ViewContainerRef } from '@angular/core';
import { ChartComponent } from '../../chart/chart.component';
import { BarChart } from '../../chart/classes/bar-chart.class';
import { GroupedBarChart } from '../../chart/classes/grouped-bar.class';
import { DonutChart } from '../../chart/classes/donut-chart.class';
import { StackBarChart } from '../../chart/classes/stack-bar.class';
import { HorizontalStackBar } from '../../chart/classes/horisontal-stack-bar.class';
import { MonitoringService } from '../../../monitoring.service';
import { Store } from '@ngrx/store';
import { AppState } from '../../../../../core/store/state.interface';
import { selectPageView } from '../../../../../core/store/monitoring/monitoring.selector';
import { filter } from 'rxjs';

@Component({
  selector: 'app-usage-graph',
  templateUrl: './usage-graph.component.html',
  styleUrl: './usage-graph.component.scss'
})
export class UsageGraphComponent implements OnInit {

  constructor(
    private monitoringService: MonitoringService,
    private store: Store<AppState>,
  ){}

  @ViewChild('container', {read: ViewContainerRef, static: true}) container!: ViewContainerRef;

  ngOnInit(): void {
    // this.initData();
    this.drawChart('login');
    this.drawChart('monitoring');
    this.drawGroup();
    this.drawStack();
    this.drawDonut();
    // this.drawHrizontalStack();
  }

  // initData(){
    // this.monitoringService.getData().subscribe({
    //   next: d => {
    //     console.log('데이터', d);
        
    //     this.monitoringService.data$.next(d);
    //   }
    // });
  // }

  drawChart(path: string){
    const componentRef = this.container.createComponent(ChartComponent)
    const instance = componentRef.instance;
    const barChart = new BarChart(instance.container, instance.elRef.nativeElement)
    componentRef.setInput('chart', barChart);
    componentRef.setInput('path', path)
    // instance.event.pipe(take(1)).subscribe(() => this.isLoading = false);
  }

  drawGroup(){
    const componentRef = this.container.createComponent(ChartComponent)
    const instance = componentRef.instance;
    const groupedBarChart = new GroupedBarChart(instance.container, instance.elRef.nativeElement)
    componentRef.setInput('chart', groupedBarChart);
  }

  drawDonut(){
    const componentRef = this.container.createComponent(ChartComponent)
    const instance = componentRef.instance;
    const groupedBarChart = new DonutChart(instance.container, instance.elRef.nativeElement)
    componentRef.setInput('chart', groupedBarChart);
  }

  drawStack(){
    const componentRef = this.container.createComponent(ChartComponent)
    const instance = componentRef.instance;
    const groupedBarChart = new StackBarChart(instance.container, instance.elRef.nativeElement)
    componentRef.setInput('chart', groupedBarChart);
  }

  drawHrizontalStack(){
    const componentRef = this.container.createComponent(ChartComponent)
    const instance = componentRef.instance;
    const groupedBarChart = new HorizontalStackBar(instance.container, instance.elRef.nativeElement)
    componentRef.setInput('chart', groupedBarChart);
  }

}
