import * as d3 from "d3";
import { Chart } from "./chart.abstract";
import { ChartTooltipComponent } from "../../chart-tooltip/chart-tooltip.component";

export class BarChart extends Chart{

    override drawChart(): void {
        this.createSvg();

        // 바가 생성될 영역
        this.g = this.svg.append('g')
            .attr('transform', `translate(${this.margin.left},${this.margin.top})`)

        const defs = this.g.append("defs")
        
        defs.append("clipPath")
            .attr("id", "clip")
            .append("rect")
            .attr("width", this.width)
            .attr("height", this.height)

        const translateValue = [-30, 0];
        defs.append('clipPath')
            .attr('id', 'stack-clip')
            .append('rect')
            .attr('width', this.width + translateValue[0])
            .attr('height', this.height)
            .attr('transform', `translate(${translateValue[0]},0)`)
            
        const interval = this.interval;

        this.y = d3.scaleLinear()
            .domain([0, d3.max(this.data, d => Number((d as any).requests)) ?? 10]).nice()
            .range([this.height, 0]).nice();

        this.g.append('g')
            // .attr("clip-path", "url(#stack-clip)")
            .attr('class', 'y-axis')
            .call(d3.axisLeft(this.y))
            .style('font-family', 'Pretendard-Regular');

        this.x = d3.scaleBand()
            // .domain(this.data.map((d: any) => d.date))
            .domain(interval)
            .range([0, this.width])
            .padding(0.2);

        this.xAxis = d3.axisBottom(this.x).tickSizeOuter(0);

        this.g.append('g')
            .attr('class', 'x-axis')
            .attr('transform', `translate(0, ${this.height})`)
            .attr("clip-path", "url(#clip)")
            .call(this.xAxis)
            .selectAll('text')
            .attr('transform', 'translate(-10,0)rotate(-60)')
            .style('text-anchor', 'end')
            .style('font-family', 'Pretendard-Regular')


        this.shapes = this.g.selectAll('.mybar')
            .data(this.data)
            .join('rect')
            .attr("clip-path", "url(#clip)")
                .attr('class', 'mybar')
                .attr('x', (d: any) => this.x(d.date)!)
                .attr('y', (d: any) => this.height)
                .attr('width', this.x.bandwidth())
                .attr('height', (d: any) => this.height - this.y(d.requests))
                .attr('fill', '#69b3a2')

        this.shapes
            .transition()
            .duration(500)
            .attr('y', (d:any) => this.y(d.requests))

        this.setZoom();
        this.setMouseOver(this.shapes);
        this.setMouseMove(this.shapes);
        this.setMouseOut(this.shapes);

        this.drawGrid();
    }

    override zoom(svg: d3.Selection<SVGSVGElement, unknown, HTMLElement, any>): void {
        const extent: [[number, number], [number, number]] = [[0, this.margin.top], [this.width, this.height - this.margin.top]];

        svg.call(
            d3.zoom<SVGSVGElement, unknown>()
                .scaleExtent([1, 8])
                .translateExtent(extent)
                .extent(extent)
                .on('zoom', this.zoomed.bind(this))
        )
    }

    override zoomed(event: d3.D3ZoomEvent<HTMLElement, any>): void {
        this.x.range([0, this.width].map(d => event.transform.applyX(d)));
        this.svg.selectAll('.mybar').attr('x', (d: any) => {
            return this.x(d.date)!
        })
            .attr('width', this.x.bandwidth());
        this.svg.selectAll('.x-axis').call(this.xAxis as any);
    }

    override mouseOver(event: any): void {
        d3.select(event.target).style('opacity', 0.5);
        this.tooltip = this.container.createComponent(ChartTooltipComponent).instance;
        this.tooltip.x = event.clientX;

        this.tooltip.y = event.clientY;

        this.tooltip.data = event.target.__data__;
    }

    override mouseMove(event: any): void {
        this.tooltip.onMouseMove(event);
    }
    
    override mouseOut(event: any): void {
        d3.select(event.target).style('opacity', 1);
        this.container.clear();
    }

    override refineData(data: any, url: any) {
        this.data = data.filter((d: any) => d.url === url)?? [];
        return data.filter((d: any) => d.url === url);
    }

}