import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AreaExamleComponent } from './components/area-examle/area-examle.component';
import { D3chartComponent } from './components/d3chart/d3chart.component';
import { MonitoringLayoutComponent } from './components/monitoring-layout/monitoring-layout.component';

const routes: Routes = [
  {path: '', component: MonitoringLayoutComponent},
  {path: 'area', component: AreaExamleComponent},
  {path: 'bar', component: D3chartComponent}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class MonitoringRoutingModule { }
