import { ViewContainerRef } from "@angular/core";
import * as d3 from 'd3';
import { ChartTooltipComponent } from "../../chart-tooltip/chart-tooltip.component";
import { Subject } from "rxjs";
import { DateTime, Interval } from "luxon";

export interface ChartConfig{
    container: ViewContainerRef;
    elementRef: HTMLElement;
    dataSubject: Subject<any>;
}

export abstract class Chart {

    constructor(
        protected container: ViewContainerRef,
        protected elementRef: HTMLElement
    ){ 
        // this.svg = (d3.select(this.elementRef).select('#chart') as any)
        //     // 차트가 그려질 svg 영역
        //     .append('svg')
        //     .attr('width', this.width + this.margin.left + this.margin.right)
        //     .attr('height', this.height + this.margin.top + this.margin.bottom)
    }
    
    data: any;
    margin = {
        top: 10, right: 30, bottom: 60, left: 30
    }
    width = 460 - this.margin.left - this.margin.right;
    height = 400 - this.margin.top - this.margin.bottom;

    /**
     * Selection
     * The first generic "GElement" refers to the type of the selected element(s).
     * The second generic "Datum" refers to the type of the datum of a selected element(s).
     * The third generic "PElement" refers to the type of the parent element(s) in the D3 selection.
     * The fourth generic "PDatum" refers to the type of the datum of the parent element(s).
     */
    svg: d3.Selection<SVGSVGElement, unknown, HTMLElement, any> = {} as d3.Selection<SVGSVGElement, unknown, HTMLElement, any>;
    g: d3.Selection<SVGGElement, unknown, HTMLElement, any> = {} as d3.Selection<SVGGElement, unknown, HTMLElement, any>;
    shapes: d3.Selection<d3.BaseType | SVGRectElement, any, any, any> = {} as d3.Selection<d3.BaseType | SVGRectElement, any, any, any>;
    x: d3.ScaleBand<string> = {} as d3.ScaleBand<string>;
    y: d3.ScaleLinear<number, number, never> = {} as d3.ScaleLinear<number, number, never>;
    subX: d3.ScaleBand<string> = {} as d3.ScaleBand<string>;
    xAxis: d3.Axis<string> = {} as d3.Axis<string>;
    yAxis: d3.Axis<d3.NumberValue> = {} as d3.Axis<d3.NumberValue>;
    groups: any[] = [];
    subgroups: any[] = [];
    interval: string[] = [];

    tooltip!: ChartTooltipComponent;

    yGridline: d3.Selection<SVGLineElement, any, HTMLElement, any> = {} as d3.Selection<SVGLineElement, any, HTMLElement, any>;

    createSvg(): void {
        this.svg = (d3.select(this.elementRef).select('#chart') as any)
            // 차트가 그려질 svg 영역
            .append('svg')
            .attr('width', this.width + this.margin.left + this.margin.right)
            .attr('height', this.height + this.margin.top + this.margin.bottom)
    }

    abstract drawChart(): void;

    setZoom(): void {
        this.svg.call(this.zoom.bind(this));
    };

    setMouseOver(selection: d3.Selection<any, any, any, any>):void {
        selection.on('mouseover', (d) => this.mouseOver(d))
    }

    setMouseMove(selection: d3.Selection<any, any, any, any>): void{
        selection.on('mousemove', (d) => this.mouseMove(d))
    }

    setMouseOut(selection: d3.Selection<any, any, any, any>): void{
        selection.on('mouseout', (d) => this.mouseOut(d))
    }

    drawGrid(){
        this.yGridline = d3.selectAll('g.y-axis g.tick')
            .append('line')
            .attr('class', 'gridline')
            .attr('x1', 0)
            .attr('y1', 0)
            .attr('x2', this.width)
            .attr('y2', 0)
            .attr('stroke', '#cde8d4')
            .attr('stroke-dash', '4')
    }

    removeChart(){
        if(Object.keys(this.svg).length > 0){
            this.svg.remove();
        }
    }

    setInterval(month: DateTime){
        this.interval = Interval.fromDateTimes(
            month.startOf('month'),
            month.endOf('month')
        ).splitBy({
            day: 1
        }).map((date: Interval) => date.start?.toISODate()) as string[];
    }

    abstract zoom(svg: d3.Selection<SVGSVGElement, unknown, HTMLElement, any>): void;
    abstract zoomed(event: d3.D3ZoomEvent<HTMLElement, any>): void;
    abstract mouseOver(event: any): void;
    abstract mouseMove(event: any): void;
    abstract mouseOut(event: any): void;
    abstract refineData(data: any, url?: any): any;

}